<template>
  <div class="practice">
    <div class="max-container" id="container">
      <side-line></side-line>
      <div class="main-container" id="main-container" v-loading="loading">
        <div class="participate">
          <div class="header">
            <h2></h2>
            <p style="font-weight: bold;"></p>
          </div>
          <div class="choice">
            <p class="noCreate" style="color:red; margin:-45px 0 0 0px; padding-bottom:40px;"> 6270:00</p>
            <div class="choice-1">
              <p style="height:50px; margin-top:-30px;">1. </p>
              <el-button type="primary" id="create-button" @click="toCreate()"></el-button>
            </div>
            <div class="choice-2">
              <p style="height:50px; margin-top:-20px">2. </p>
              <el-button type="primary" id="join-button" @click="toEnter()"></el-button>
            </div>
          </div>
        </div>
        <el-button id="check-button" type="primary" @click="viewTeams()"></el-button>
      </div>
    </div>
  </div>
</template>

<script>
import SideLine from "./SideLine";
import { mapGetters, mapMutations } from "vuex";
import { getAllTeamInfo } from "@/api/team";
import { setTimeout } from "timers";

export default {
  name: "practice",
  components: {
    SideLine
  },
  data() {
    return {
      loading: true
    };
  },
  computed: {
    ...mapGetters({
      cas_user: "cas_user",
      active: "active",
      secondLoad: "secondLoad"
    }),
    active() {
      if (this.cas_user.has_details == 1) {
        return 1;
      } else {
        return 0;
      }
    }
  },
  created() {
    setTimeout(() => {
      this.reload();
    }, 200);
    setTimeout(() => {
      this.loading = false;
    }, 500);
    document.getElementById("main-layout-main").scrollIntoView({block:"end",behavior:"smooth"});
  },
  mounted() {
    document.getElementById("main-layout-main").scrollIntoView({block:"end",behavior:"smooth"});
  },
  methods: {
    ...mapMutations({
      setUncreated: "setUncreated",
      setActive: "setActive"
    }),
    async getTeams() {
      try {
        const data = await getAllTeamInfo();
        if (!data.total == 0) {
          this.setActive(1);
        }
      } catch (message) {
        this.$message.warning(message);
      }
    },
    toCreate() {
      this.setUncreated(true);
      this.$router.push("/practice/getinfo");
    },
    toEnter() {
      this.$router.push("/practice/enter");
    },
    viewTeams() {
      this.$router.push("/practice/teaminf");
    },
    async reload() {
      try {
        if (this.cas_user.has_details == 0) {
          this.$router.push("/practice/getselfinfo");
        } else if (this.cas_user.has_details == 1) {
          if (!this.active == 0 && !this.secondLoad == 0) {
            this.$router.push("/practice/teaminf");
          } else {
            this.$router.push("/practice");
          }
        }
        const data = await this.getTeams();
      } catch (message) {
        this.$message.warning(message);
      }
    }
  }
};
</script>

