// requestaxiosaxios
import request from './request'

// 
export function postProfileInfo(ruleForm) {
  // axios
  // Promise
  return request({
    url: '/user/profile',
    method: 'post',
    data: {
      form: ruleForm   //
    }
  })
}

// 
export function getProfileInfo() {
  return request({
    url: '/user/profile',
    method: 'get',
  })
}

// 
export function putProfileInfo(ruleForm) {
  return request({
    url: '/user/profile',
    method: 'put',
    data: ruleForm   //form
  })
}

// 
export function deleteProfileInfo() {
  return request({
    url: '/user/profile',
    method: 'delete',
  })
}

//user
export function getUserInfo() {
  return request({
    url: '/user/info',
    method: 'get',
  })
}