const typeToModeMap = {
  'input': '',
  'select': '',
  'cascader': '',
  'textarea': '',
  'daterange': ''
}

const typeToTriggerMap = {
  'input': 'blur',
  'select': 'change',
  'cascader': 'change',
  'textarea': 'blur',
  'daterange': 'change'
}

function typeToMode(type) {
  let mode = typeToModeMap[type]
  if (mode === undefined) {
    mode = ''
  }
  return mode
}

export function initMirror(mirror) {
  // mirror
  const m = Object.assign({}, mirror)
  // placeholder
  const mode = typeToMode(m.type)
  const label = m.label
  const message = `${mode}${label}`
  // placeholder
  if (m.placeholder === undefined || m.placeholder === '') {
    m.placeholder = message
  }
  // rule
  let rules = []
  const trigger = typeToTriggerMap[m.type]
  if (trigger && !m.disableRule) {
    rules.push({
      required: true,
      message,
      trigger
    })
  }
  // rules
  if (m.moreRules) {
    rules = rules.concat(m.moreRules)
    delete m.moreRules
  }
  // 
  m.rules = rules
  return m
}
