<template>
  <div class="manage-examine manage-dev">
    <div class="title-line">
      <p class="title">
        
      </p>
      <p class="org">
        {{ BELONGTO_LIST[this.user_info.org] }}
      </p>
    </div>
    <!--  -->
    <div v-if="canPanel" class="select-belong">
      <div class="select-display">
        <FitForm ref="form_belong" :mirrors="mirrors_belongs" />
        <FitForm ref="form_type" :mirrors="mirrors_type" />
      </div>
      <div class="select-button">
        <el-button class="belongs-button" type="success" @click="select()">
          
        </el-button>
        <el-button class="belongs-button" type="info" @click="resetAll()">
          
        </el-button>
      </div>
    </div>
    <el-tabs v-model="status" class="table-tab" @tab-click="handleClick">
      <el-tab-pane label="" name="all"></el-tab-pane>
      <el-tab-pane label="" name="4"></el-tab-pane>
      <el-tab-pane label="" name="5"></el-tab-pane>
      <el-tab-pane label="" name="7"></el-tab-pane>
      <el-tab-pane label="" name="6"></el-tab-pane>
      <el-tab-pane label="" name="8"></el-tab-pane>
    </el-tabs>
    <!-- <p class="manage-examine-remain title">{{ pagination.total }}</p> -->
    <pager
      ref="refresh"
      :page-size="6"
      :request-func="getList"
      :error-msg="''"
      :extra-data="status"
      :belong-data="belongs"
      :type-data="project_types"
    >
      <el-table
        v-loading="loading"
        slot-scope="props"
        border
        class="table-project"
        :data="props.list"
      >
        <el-table-column label="" width="160">
          <template slot-scope="scope">
            <div class="line">
              {{ scope.row.project_name }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="" width="220">
          <template slot-scope="scope">
            <div class="line">
              {{ scope.row.place }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <div class="line">
              {{ scope.row.status | statusToReadable }}
            </div>
          </template>
        </el-table-column>
        <el-table-column
          label=""
          width="200"
        >
          <template slot-scope="scope">
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
            >
              <el-button
                v-show="canPass(scope.row)"
                icon="el-icon-success"
                circle
                type="success"
                class="button-lists-button"
                size="small"
                @click="projectPass(scope.row)"
              ></el-button>
            </el-tooltip>
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
            >
              <el-button
                v-show="canRecommon(scope.row)"
                icon="el-icon-arrow-up"
                circle
                type="success"
                class="button-lists-button"
                size="small"
                @click="projectRecommon(scope.row)"
              ></el-button>
            </el-tooltip>
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
            >
              <el-button
                v-show="canReject(scope.row)"
                icon="el-icon-error"
                circle
                type="danger"
                class="button-lists-button"
                size="small"
                @click="projectReject(scope.row)"
              ></el-button>
            </el-tooltip>
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
            >
              <el-button
                v-show="canRecRej(scope.row)"
                icon="el-icon-arrow-down"
                circle
                type="danger"
                class="button-lists-button"
                size="small"
                @click="projectRecRej(scope.row)"
              ></el-button>
            </el-tooltip>
            <el-tooltip
