                @click="Suggest(scope.row)"
              ></el-button>
            </el-tooltip>
            <el-tooltip
              class="item"
              effect="dark"
              content=""
              placement="top"
            >
              <el-button
                type="info"
                class="button-lists-button"
                icon="el-icon-more"
                circle
                size="small"
                @click="openDialog(scope.row)"
              ></el-button>
            </el-tooltip>
          </template>
        </el-table-column>
      </el-table>
    </pager>
    <el-dialog
      v-dialogDrag
      title=""
      :fullscreen="isPhone"
      :visible.sync="dialogVisible"
    >
      <TeamInfoDisplay :team-info="dialogRow" :teammates-info="teammates" />
      <!--    -->
      <el-button
        round
        type="success"
        :disabled="!canPass(dialogRow)"
        @click="projectPass(dialogRow)"
      >
        
      </el-button>
      <!--    -->
      <el-button
        round
        type="danger"
        :disabled="!canReject(dialogRow)"
        @click="projectReject(dialogRow)"
      >
        
      </el-button>
      <!--    -->
      <el-button
        round
        type="success"
        :disabled="!canRecommon(dialogRow)"
        @click="projectRecommon(dialogRow)"
      >
        
      </el-button>
      <!--    -->
