        type="textarea"
        class="suggest-content"
        :rows="4"
        :cols="10"
        placeholder=""
      >
      </el-input>
      <el-button
        round
        type="success"
        @click="submitSuggestion()"
      >
        
      </el-button>
      <el-button round type="info" @click="suggestDialogVisible = false">
        
      </el-button>
    </el-dialog>
  </div>
</template>

<script>
import Pager from '@/components/ArtPager'
import TeamInfoDisplay from '@/components/TeamInfoDisplay'
import statusMixin from '@/mixins/status'
import { mapGetters, mapMutations } from 'vuex'
import { showAsynConfirm } from '@/mixins/messageBox'
import { getTeammateInfo, getAdminTeams, teamIsPass, suggestion, toSchoolLevel, cancelToSchool } from '@/api/team'
import FitForm from '@/components/FitForm.vue'
import { BELONGTOS, PRACTICE_TYPES } from '@/mixins/consts.js'

const mirrors_belongs = [
  {
    type: 'select',
    label: '',
    prop: 'belongs',
    options: BELONGTOS
  }
]

const mirrors_type = [
  {
    type: 'select',
    label: '',
    prop: 'project_types',
    options: PRACTICE_TYPES
  }
]

export default {
  name: 'ManageExamine',
  components: {
    Pager,
    TeamInfoDisplay,
    FitForm
  },
  mixins: [statusMixin],
  data() {
    return {
      mirrors_belongs,
      mirrors_type,
      loading: false,
      status: 'all',
      // 
      belongs: 0,
      // 
      project_types: 0,
      // 
      suggestion: '',
      suggestDialogVisible: false,
      projects: [],
      teammates: [],
      // 
      dialogVisible: false,
      dialogRow: {},
      BELONGTO_LIST: [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ],
      PRACTICE_TYPE_LIST: [
        '',
        '70',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ]
    }
  },
  computed: {
    ...mapGetters({
      user_info: 'user_info',
      isPhone: 'isPhone',
      currentTeamId: 'currentTeamId',
      canPanel: 'canPanel'
    })
  },
  mounted() {
    document.getElementById('manage-examine').scrollIntoView()
  },
  methods: {
    ...mapMutations({
      saveCurrentTeamId: 'saveCurrentTeamId'
    }),
    // pager
    getList: getAdminTeams,
    async getTeams() {
      this.loading = true
      try {
        const data = await getAdminTeams(this.status)
        this.projects = data.data
        this.loading = false
      } catch (message) {
        this.$message({
          type: 'error',
          message: '  ' + message
        })
      }
    },
    async getTeammateInfo() {
      this.loading = true
      try {
        const data = await getTeammateInfo(this.currentTeamId)
        this.teammates = data.data
        this.loading = false
      } catch (message) {
        this.$message({
          type: 'error',
          message: '  ' + message
        })
      }
    },
    // belongs
    // belongs
    async select() {
      const form_1 = this.$refs['form_belong']
      const form_2 = this.$refs['form_type']
      const data_1 = form_1.getData()
      const data_2 = form_2.getData()
      console.log(data_1, data_2)
      this.belongs = data_1.belongs
      this.project_types = data_2.project_types
      this.loading = true
      setTimeout(() => {
        this.changePages(1)
        this.loading = false
      }, 500)
    },
    // 
    resetAll() {
      const form_1 = this.$refs['form_belong']
      const form_2 = this.$refs['form_type']
      const data_1 = form_1.setData(0)
      const data_2 = form_2.setData(0)
      console.log(data_1, data_2)
      this.belongs = 0
      this.project_types = 0
      this.loading = true
      setTimeout(() => {
        this.changePages(1)
        this.loading = false
      }, 500)
    },
    // 
    async _teamIsPass(team_id, data) {
      try {
        const return_data = await teamIsPass(team_id, data)
        this.$message({
          type: 'success',
          message: '!'
        })
        this.changePages(1)
      } catch (message) {
        this.$message({
          type: 'error',
          message: '' + message
        })
      }
    },
    async _toSchoolLevel(team_id) {
      try {
        const return_data = await toSchoolLevel(team_id)
        this.changePages(1)
        this.$message({
          type: 'success',
          message: ''
        })
      } catch (message) {
        this.$message({
          type: 'error',
          message: '' + message
        })
      }
    },
    async _cancelToSchool(team_id) {
      try {
        const return_data = await cancelToSchool(team_id)
        this.changePages(1)
        this.$message({
          type: 'success',
          message: ''
        })
      } catch (message) {
        this.$message({
          type: 'error',
          message: '' + message
        })
      }
    },
    _check(row, msg, boolean) {
      showAsynConfirm(msg, '', (done) => {
        this._teamIsPass(row.team_id, boolean)
        done()
        this.changePages(1)
      })
    },
    _checkRec(row, msg, boolean) {
      showAsynConfirm(msg, '', (done) => {
        if (boolean) {
          this._toSchoolLevel(row.team_id)
        } else if (!boolean) {
          this._cancelToSchool(row.team_id)
        }
        done()
        this.changePages(1)
      })
    },
    // 
    projectPass(row) {
      this._check(row, '', 1)
    },
    // 
    projectReject(row) {
      this._check(row, '', 0)
    },
    // 
    projectRecommon(row) {
      this._checkRec(row, '', 1)
    },
    // 
    projectRecRej(row) {
      this._checkRec(row, '', 0)
    },
    // 
    canPass(row) {
      return row.status == 5 || row.status == 6
    },
    // 
    canReject(row) {
      return row.status == 5 || row.status == 7
    },
    canRecommon(row) {
      return row.status == 7
    },
    canRecRej(row) {
      return row.status == 8
    },
    // 
    changePages(i) {
      this.$refs.refresh.turn(i)
      this.dialogVisible = false
    },
    // 
    handleClick() {
      setTimeout(() => {
        this.changePages(1)
      }, 200)
    },
    // teams
    openDialog(row) {
      this.dialogRow = row
      this.saveCurrentTeamId(row.team_id)
      setTimeout(() => {
        this.getTeammateInfo()
      }, 100)
      setTimeout(() => {
        this.dialogVisible = true
      }, 300)
    },
    // dialog
    Suggest(row) {
      this.suggestDialogVisible = true
      this.timelyRow = row
      this.saveCurrentTeamId(row.team_id)
    },
    submitSuggestion() {
      showAsynConfirm('', '', (done) => {
        this._submitSuggestion(done)
      })
    },
    async _submitSuggestion(done) {
      done()
      try {
        const data = await suggestion(this.currentTeamId, this.suggestion)
        this.$message({
          type: 'success',
          message: ''
        })
        this.suggestDialogVisible = false
        this.suggestion = ''
        this.changePages(1)
      } catch (message) {
        this.$message({
          type: 'error',
          message: '  ' + message
        })
      }
    }
  }
}
</script>

