export default {
  filters: {
    statusToReadable(val) {
      const map = {
        0: '',
        1: '',
        2: '',
        3: '',
        4: '',
        5: '',
        6: '',
        7: '',
        8: ''
      }
      return map[val] || ''
    },
    typeToReadable(val) {
      const map = {
        1: '',
        2: '',
        3: '',
        4: ''
      }
      return map[val] || ''
    }
  },
  computed: {
    noticeStatusColor() {
      // 1234
      const map = {
        1: '#000000',
        2: '#ea1e63',
        3: '#4cb050',
        4: '#1976d3'
      }
      return map[this.status] || 'grey'
    }
  }
}

