// requestaxiosaxios
import request from './request'

// 
export function getCurrentAllmessage(type) {
  return request({
    url: '/messages?status=' + type,
    method: 'get',
  })
}

// 
export function getOnemessage(id) {
  return request({
    url: '/message/' + id,
    method: 'get',
  })
}

// 
export function getAllRead() {
  return request({
    url: '/message/allRead',
    method: 'put',
  })
}

