// requestaxiosaxios
import request from './request'

// 
export function getAllGuide(form) {
  return request({
    url: '/guide/getAll',
    method: 'get',
    params: form
  })
}

// 
export function getOneGuide(id) {
  return request({
    url: '/guide/' + id,
    method: 'get',
  })
}

// 
export function deleteOneGuide(id) {
  return request({
    url: '/admin/guide/' + id,
    method: 'delete',
  })
}

// 
export function addOneGuide(form) {
  return request({
    url: '/admin/guide',
    method: 'post',
    data: form
  })
}