// requestaxiosaxios
import request from './request'

// 
export function postTeamInfo(form) {
  // axios
  // Promise
  return request({
    url: '/team',
    method: 'post',
    data: form
  })
}

// 
export function getAllTeamInfo(form) {
  return request({
    url: '/teams',
    method: 'get',
    params: form
  })
}

// 
export function EditTeam(id, form) {
  return request({
    url: '/team/' + id,
    method: 'put',
    data: form
  })
}

//  id
export function getOneTeamInfo(id) {
  return request({
    url: '/team/' + id,
    method: 'get'
  })
}

// 
export function Submit(id) {
  return request({
    url: '/submitTeam/' + id,
    method: 'put'
  })
}

