  data() {
    var checkNumber = (rule, value, callback) => {
      var pattern = /^\d+.?\d{0,2}$/
      if (!value) {
        return callback(new Error(''))
      }
      setTimeout(() => {
        if (Number(value) > 0) {
          callback()
        } else {
          callback(new Error(''))
        }
      }, 500)
    }
    var onlyCheckNumber = (rule, value, callback) => {
      if (Number(value) > 0) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    var CheckActive = (rule, value, callback) => {
      if (value == 0 || value == 1) {
        callback()
      } else {
        callback(new Error(''))
      }
    }
    var checkViceName = (rule, value, callback) => {
      if (this.ruleForm.vice_leader_name == null) {
        return callback(new Error(''))
      } else {
        callback()
      }
    }
    var checkTime = (rule, value, callback) => {
      if (isNaN(this._plan_begin_time) || isNaN(this._plan_end_time)) {
        return callback(new Error(''))
      } else {
        callback()
      }
    }

    return {
      teams: [],
      practice_types: [
        {
          value: '1',
          label: '70'
        },
        {
