      ruleForm: {
        team_name: '',
        project_name: '',
        place: '',
        project_type: '',
        plan_region_time: '',
        delivery: false,
        event_content: '',
        event_background: '',
        event_aim: '',
        fee_transport: '',
        fee_print: '',
        fee_other: '',
        // 
        vice_leader_net_id: '',
        vice_leader_id: '',
        vice_leader_name: '',
