        place: [{ required: true, message: '', trigger: 'blur' }],
        plan_region_time: [
          { validator: checkTime, required: true, trigger: 'blur' }
        ],
        project_type: [
          { required: true, message: '', trigger: 'change' }
        ],
        event_background: [
          { required: true, message: '', trigger: 'blur' }
        ],
        event_aim: [
          { required: true, message: '', trigger: 'blur' }
        ],
        event_content: [
