          { validator: checkNumber, required: true, trigger: 'blur' }
        ],
        fee_print: [
          { validator: checkNumber, required: true, trigger: 'blur' }
        ],
        fee_other: [
          { validator: onlyCheckNumber, required: false, trigger: 'blur' }
        ],
        vice_leader_net_id: [
          { required: true, message: 'NetID', trigger: 'blur' }
        ],
        vice_leader_name: [
          { validator: checkViceName, required: true, trigger: 'blur' }
        ],
        teacher_name: [
          { required: true, message: '', trigger: 'blur' }
        ],
        belong: [
          { required: true, message: '', trigger: 'change' }
        ],
        department: [
          { required: false, message: '', trigger: 'blur' }
        ],
        teacher_follow: [
          { validator: CheckActive, required: true, trigger: 'change' }
        ]
      }
    }
  },
  computed: {
    ...mapGetters({
      uncreated: 'uncreated',
      active: 'active',
      team: 'team',
      isEdit: 'isEdit',
      currentTeamId: 'currentTeamId',
      showDepartment: 'showDepartment'
    }),
    ...mapMutations({
      setUncreated: 'setUncreated'
    }),
    _plan_begin_time() {
      return parseInt(this.ruleForm.plan_region_time[0] / 1000)
    },
    _plan_end_time() {
      return parseInt(this.ruleForm.plan_region_time[1] / 1000)
    },
    has_created() {
      return false
    }
  },
  mounted() {
    document.getElementById('container').scrollIntoView({ block: 'end', behavior: 'smooth' })
  },
  created() {
    this.getEditForm()
    const keys = [
      'team_name',
      'project_name',
      'event_background',
      'event_aim',
      'fee_transport',
      'fee_print',
      'place',
      'plan_region_time',
      'delivery',
      'event_content',
      'fee_other',
      'vice_leader_net_id',
      'vice_leader_id',
      'teacher_name',
      'vice_leader_name',
      'belong',
      'teacher_follow',
      'department'
    ]
    for (const i in keys) {
      if (!(typeof (localStorage[keys[i]]) === 'undefined') && this.uncreated == true) { this.ruleForm[keys[i]] = localStorage.getItem(keys[i]) }
    }
  },
  methods: {
    ...mapMutations({
    }),
    // 
    onSubmit() {
      console.log('onSubmit():')
      console.log(this.ruleForm)
      this.$refs['ruleForm'].validate(valid => {
        if (valid) {
          this.submitForm()
        } else {
          this.$message({
            type: 'warning',
            message: ''
          })
          return false
        }
      })
    },
    // 
    submitForm() {
      showAsynConfirm('', '', done => {
        this._submitForm(done)
      })
    },
    // 
    async _submitForm(done) {
      try {
        done()
        const params = this.parseForm()
        console.log('submitForm():'); console.log(params); console.log(this.ruleForm)
        const data = await postTeamInfo(params)
        this.$message({
          type: 'success',
          message: ''
        })
        this.toNext()
      } catch (err_msg) {
        done()
        this.$message({
          type: 'warning',
          message: err_msg
        })
      }
    },
    //
    parseForm() {
      // 
      const keys = [
        'team_name',
        'project_name',
        'event_background',
        'event_aim',
        'fee_transport',
        'fee_print',
        'place',
        'project_type',
        'plan_region_time',
        'event_content',
        'fee_other',
        'vice_leader_id',
        'teacher_name',
        'belong',
        'teacher_follow',
        'department'
      ]
      const params = {}
      console.log(this.ruleForm.teacher_follow)
      for (const i in keys) {
        params[keys[i]] = this.ruleForm[keys[i]]
      }
      // 
      const begin_time = parseInt(this.ruleForm.plan_region_time[0] / 1000)
      const end_time = parseInt(this.ruleForm.plan_region_time[1] / 1000)
      params.plan_begin_time = this.timestampToTime(begin_time)
      params.plan_end_time = this.timestampToTime(end_time)
      if (isNaN(params.belong)) {
        params.belong = this.team.belong
      }
      if (isNaN(params.project_type)) {
        params.project_type = this.team.project_type
      }
      return params
    },
    parseEditForm() {
      // 
      const keys = [
        'team_name',
        'project_name',
        'project_type',
        'place',
        'event_background',
        'event_aim',
        'event_content',
        'fee_transport',
        'fee_print',
        'fee_other',
        'belong',
        'teacher_follow',
        'vice_leader_id',
        'vice_leader_name',
        'teacher_name',
        'practice_type',
        'department'
      ]
      console.log(this.team)
      console.log(this.ruleForm)
      for (const i in keys) {
        this.ruleForm[keys[i]] = this.team[keys[i]]
      }
      // 
      // plan_region_time
      const beginTime = Math.floor(
        new Date(this.team.plan_begin_time).getTime() / 1000
      )
      const endTime = Math.floor(
        new Date(this.team.plan_end_time).getTime() / 1000
      )
      // netId
      this.ruleForm.vice_leader_net_id = this.team.vice_net_id
      // 
      this.ruleForm.belong = this.BELONGTO_LIST[this.team.belong]
      // 
      this.ruleForm.project_type = this.PRACTICE_TYPE_LIST[this.team.project_type]
    },
    getEditForm() {
      if (!this.uncreated) {
        this.parseEditForm()
      }
    },
    submitEditForm() {
      showAsynConfirm('', '', done => {
        this._submitEditForm(done)
      })
    },
    async _submitEditForm(done) {
      try {
        const data = await EditTeam(this.currentTeamId, this.parseForm())
        this.$message({
          type: 'success',
          message: ''
        })
        done()
        this.toNext()
      } catch (message) {
        this.$message({
          type: 'warning',
          message: ''
        })
      }
    },
    // net-id
    async searchViceLeader() {
      try {
        this.$message({
          type: 'primary',
          message: '.....'
        })
        const data = await getByNetId(this.ruleForm.vice_leader_net_id)
        const leader_data = await getUserInfo()
        const leader_id = leader_data.user_id
        if (data.user_id != leader_id) {
          this.ruleForm.vice_leader_name = data.user_name
          this.ruleForm.vice_leader_id = data.user_id
          this.$message({
            type: 'success',
            message: ''
          })
        }
        if (data.user_id == leader_id) {
          this.ruleForm.vice_leader_name = '',
          this.$message({
            type: 'warning',
            message: ''
          })
        }
      } catch (message) {
        this.$message({
          type: 'warning',
          message: message
        })
      }
    },
    // 
    timestampToTime(timestamp) {
      var date = new Date(timestamp * 1000) // 10*1000131000
      const Y = date.getFullYear() + '-'
      const M =
        (date.getMonth() + 1 < 10
          ? '0' + (date.getMonth() + 1)
          : date.getMonth() + 1) + '-'
      const D = this.change(date.getDate()) + ' '
      const h = this.change(date.getHours()) + ':'
      const m = this.change(date.getMinutes()) + ':'
      const s = this.change(date.getSeconds())
      return Y + M + D + h + m + s
    },
    change(t) {
      if (t < 10) {
        return '0' + t
      } else {
        return t
      }
    },
    toNext() {
      this.$router.push('/practice/teaminf')
      this.clearStorage()
    },
    editToNext() {
      this.$router.push('/practice/teaminf')
      this.clearStorage()
    },
    // 
    resetForm(formName) {
      this.$refs[formName].resetFields()
      this.clearStorage()
      document.getElementById('container').scrollIntoView({ block: 'end', behavior: 'smooth' })
    },
    clearStorage() {
      const keys = [
        'team_name',
        'project_name',
        'event_background',
        'event_aim',
        'fee_transport',
        'fee_print',
        'place',
        'project_type',
        'plan_region_time',
        'delivery',
        'event_content',
        'fee_other',
        'vice_leader_net_id',
        'vice_leader_id',
        'teacher_name',
        'vice_leader_name',
        'belong',
        'teacher_follow',
        'department'
      ]
      for (const i in keys) {
        localStorage.setItem(keys[i], '')
        this.ruleForm[keys[i]] = ''
      }
    }
  },
  beforeDestroy: function() {
    const keys = [
      'team_name',
      'project_name',
      'event_background',
      'event_aim',
      'fee_transport',
      'fee_print',
      'place',
      'project_type',
      'plan_region_time',
      'delivery',
      'event_content',
      'fee_other',
      'vice_leader_net_id',
      'teacher_name',
      'vice_leader_id',
      'vice_leader_name',
      'belong',
      'teacher_follow',
      'department'
    ]
    if (this.has_created == false) {
      if (this.uncreated == false) {
        localStorage.setItem('uncreated', this.uncreated)
      }
      for (const i in keys) {
        localStorage.setItem(keys[i], this.ruleForm[keys[i]])
      }
    }
    if (this.uncreated == false) {
      this.clearStorage()
    }
  }
}
</script>

