<template>
  <div class="main-container manage-dev">
    <div class="Teams">
      <p class="title">
        
      </p>
      <pager
        ref="refresh"
        :page-size="6"
        :request-func="getList"
        :error-msg="''"
        :extra-data="status"
      >
        <el-table slot-scope="props" v-loading="loading" border :data="props.list">
          <el-table-column label="">
            <template slot-scope="scope">
              <div class="line">
                {{ scope.row.team_name }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <div class="line">
                {{ scope.row.project_name }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="" min-width="250px;">
            <template slot-scope="scope">
              <div class="info">
                {{ scope.row.leader_id }}
              </div>
              <div class="info">
                {{ scope.row.teacher_name }}
              </div>
              <div class="info">
                {{ scope.row.belong }}
              </div>
            </template>
          </el-table-column>
          <el-table-column label="" width="200">
            <template slot-scope="scope">
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  type="info"
                  class="button-lists-button"
                  icon="el-icon-more"
                  circle
                  size="small"
                  @click="openDialog(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  icon="el-icon-edit"
                  circle
                  type="primary"
                  size="small"
                  class="button-lists-button"
                  @click="Suggest(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  icon="el-icon-close"
                  type="danger"
                  size="small"
                  class="button-lists-button"
                  @click="Dismiss(scope.row)"
                >
                  
                </el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </pager>
      <el-dialog v-dialogDrag title="" :visible.sync="dialogVisible" :fullscreen="isPhone">
        <TeamInfoDisplay :team-info="dialogRow" :teammates-info="teammates" />
      </el-dialog>
      <el-dialog
        v-dialogDrag
        title=""
        :visible.sync="suggest.visible"
        :fullscreen="isPhone"
        class="suggest-dialog"
      >
        <div class="suggest-content">
          <el-input
            v-model="suggest.row.content"
            type="textarea"
            class="suggest-content"
            :rows="4"
            :cols="10"
            placeholder=""
          ></el-input>
          <el-button type="primary" @click="submitSuggestion()">
            
          </el-button>
          <el-button @click="suggest.visible = false">
            
          </el-button>
        </div>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import axios from 'axios'
import { mapGetters, mapMutations } from 'vuex'
import Pager from '@/components/ArtPager'
import TeamInfoDisplay from '@/components/TeamInfoDisplay'
import {
  getTeammateInfo,
  getAdminTeams,
  getOneAdminTeam,
  suggestion,
  dismissTeam
} from '@/api/team'
import { showAsynConfirm } from '@/mixins/messageBox'

export default {
  name: 'ManageTeam',
  components: {
    Pager,
    TeamInfoDisplay
  },
  data() {
    return {
      dialogVisible: false,
      dialogRow: {},
      teammates: [],
      suggest: {
        visible: false,
        row: {
          team_id: '',
          content: ''
        }
      },
      status: 'all',
      teams: null,
      loading: false
    }
  },
  computed: {
    ...mapGetters({
      isPhone: 'isPhone',
      currentTeamId: 'currentTeamId'
    })
  },
  mounted() {
    document.getElementById('manage-team').scrollIntoView({ block: 'end', behavior: 'smooth' })
  },
  methods: {
    ...mapMutations({
      saveCurrentTeamId: 'saveCurrentTeamId'
    }),
    // pager
    getList: getAdminTeams,
    async getTeams() {
      this.loading = true
      try {
        const data = await getAdminTeams(this.status)
        this.teams = data.data
        this.loading = false
      } catch (message) {
        this.$message({
          type: 'error',
          message: message
        })
      }
    },
    // 
    async getTeammateInfo() {
      this.loading = true
      try {
        const data = await getTeammateInfo(this.currentTeamId)
        this.teammates = data.data
        this.loading = false
      } catch (message) {
        this.$message.warning(message)
      }
    },
    // 
    openDialog(row) {
      this.dialogRow = row
      this.saveCurrentTeamId(row.team_id)
      setTimeout(() => {
        this.getTeammateInfo()
      }, 100)
      setTimeout(() => {
        this.dialogVisible = true
      }, 300)
    },
    // 
    Suggest(row) {
      this.suggest.visible = true
      this.suggest.row.team_id = row.team_id
      this.saveCurrentTeamId(row.team_id)
    },
    submitSuggestion() {
      if (this.suggest.row.content == '') {
        this.$message({
          type: 'warning',
          message: ''
        })
      } else {
        showAsynConfirm('', '', done => {
          this._submitSuggestion(done)
        })
      }
    },
    async _submitSuggestion(done) {
      done()
      try {
        const data = await suggestion(
          this.currentTeamId,
          this.suggest.row.content
        )
        this.$message({
          type: 'success',
          message: ''
        })
        this.suggest.visible = false
        this.suggest.row.content = ''
        this.changePages(1)
      } catch (message) {
        this.$message({
          type: 'error',
          message: '  ' + message
        })
      }
    },
    Dismiss(row) {
      showAsynConfirm('', '', done => {
        this._Dismiss(done)
      })
      this.saveCurrentTeamId(row.team_id)
    },
    async _Dismiss(done) {
      try {
        const data = await dismissTeam(this.currentTeamId)
        this.$message({
          type: 'success',
          message: ''
        })
        done()
        this.changePages(1)
      } catch (message) {
        done()
        this.$message({
          type: 'error',
          message: '' + message
        })
      }
    },
    changePages(i) {
      this.$refs.refresh.turn(i)
    }
  }
}
</script>

