<template>
  <el-form
    ref="form"
    :model="form"
    :inline="true"
    :rules="rules"
  >
    <FitRow
      :mirrors="mirrorList"
      :form="form"
    />
    <slot></slot>
  </el-form>
</template>

<script>
import { initMirror } from '@/js/form'
import FitRow from './FitRow.vue'

export default {
  name: 'FitForm',
  components: {
    FitRow
  },
  props: {
    mirrors: Array
  },
  data() {
    return {
      mirrorList: [],
      rules: {},
      form: {}
    }
  },
  created() {
    // mirror
    for (const i in this.mirrors) {
      const mirror = initMirror(this.mirrors[i])
      this.mirrorList.push(mirror)
      this.rules[mirror.prop] = mirror.rules
    }
  },
  methods: {
    // 
    validate(arg) {
      this.$refs.form.validate(arg)
    },
    // 
    getData() {
      return this.form
    },
    // 
    setData(data) {
      this.form = Object.assign({}, data)
    }
  }
}
</script>
