<template>
  <div id="app">
    <router-view class="router-view" />
    <Footer class="footer"></Footer>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
import tenzor from '@/assets/tenzor.png'
import Footer from './components/Footer.vue'

export default {
  name: 'App',
  components: {
    Footer
  },
  data() {
    return {
      screen: {
        timer: false,
        width: 0
      }
    }
  },
  created() {
    this.onResize()
    this.checkBrowser()
    this.logWelcome()
  },
  methods: {
    ...mapMutations({
      saveScreenWidth: 'saveScreenWidth'
    }),
    onResize() {
      var saveSize = next => {
        const domel = document.documentElement
        if (domel) {
          this.screen.timer = next
          this.screen.width = domel.clientWidth
        } else {
          this.screen.timer = true
          this.screen.width = 0
        }
        this.saveScreenWidth(this.screen)
      }
      if (!this.screen.timer) {
        saveSize(true)
        setTimeout(() => {
          saveSize(false)
        }, 400)
      }
    },
    checkBrowser() {
      var userAgent = navigator.userAgent
      var isIE =
        userAgent.indexOf('compatible') > -1 && userAgent.indexOf('MSIE') > -1
      var isIE11 =
        userAgent.indexOf('Trident') > -1 && userAgent.indexOf('rv:11.0') > -1
      if (isIE || isIE11) {
        this.$notify.error({
          title: '',
          message:
            'IE',
          duration: 0
        })
      }
    },
    logWelcome() {
      // 
      const logger = console.log
      const fontFamily =
        '"Helvetica Neue",Helvetica,"PingFang SC","Hiragino Sans GB","Microsoft YaHei","",Arial,sans-serif'
      const fontStyle = `font-size: 22px; margin-left: 0.8em; color: #f77513; text-shadow: 0 0 10px #ccc; font-family: ${fontFamily};`
      const url = `${window.location.origin}${tenzor}`
      const style = `padding:64px 128px;line-height:128px;background:url(${url}) no-repeat;`
      logger('%c', style)
      logger('%c', fontStyle)
      logger('%c', fontStyle)
      logger(
        '%ccontact@tiaozhan.com console',
        'color:red'
      )
    }
  }
}
</script>

