<template>
  <div class="square">
    <div class="header">
      <p></p>
    </div>
    <div class="main">
      <div class="tabs">
        <el-tabs
          type="border-card"
          class="el-tab"
          :tab-position="tabPosition"
          @tab-click="handleClick()"
          v-model="activeName"
        >
          <el-tab-pane name="2" label="">
            <div v-loading="loading">
              <pager 
              :pageSize="4" 
              :requestFunc="getList" 
              :errorMsg="''" 
              :recruitData="type2" 
              ref="refresh">
                <template slot-scope="scope">
                  <!--  -->
                  <div class="no-recruit" v-show="scope.list.length == 0">
                    <p></p>
                  </div>
                  <!--  -->
                  <div class="message">
                    <RecruitLine
                      v-for="(item, key, index) in scope.list"
                      :key="index"
                      :row="item"
                      ref="line_refresh"
                      @showrow="showDetails()"
                    />
                  </div>
                </template>
              </pager>
            </div>
          </el-tab-pane>
          <el-tab-pane label="" name="0">
            <div v-loading="loading">
              <pager 
              :pageSize="4" 
              :requestFunc="getList" 
              :errorMsg="''" 
              :recruitData="type0" 
              ref="objectRecruit">
                <template slot-scope="scope">
                  <!--  -->
                  <div class="no-recruit" v-show="scope.list.length == 0">
                    <p></p>
                  </div>
                  <!--  -->
                  <div class="message">
                    <RecruitLine
                      v-for="(item, key, index) in scope.list"
                      :key="index"
                      :row="item"
                      ref="line_refresh"
                      @showrow="showDetails()"
                      v-show="item.recruit_type == 0"
                    />
                  </div>
                </template>
              </pager>
            </div>
          </el-tab-pane>
          <el-tab-pane label="" name="1">
            <div v-loading="loading">
              <pager :pageSize="4" :requestFunc="getList" :errorMsg="''" :recruitData="type1" ref="subjectRecruit">
                <template slot-scope="scope">
                  <!--  -->
                  <div class="no-recruit" v-show="scope.list.length == 0">
                    <p></p>
                  </div>
                  <!--  -->
                  <div class="message">
                    <RecruitLine
                      class="recruit-line"
                      v-for="item in scope.list"
                      :key="item.index"
                      :row="item"
                      ref="line_refresh"
                      @showrow="showDetails()"
                      v-show="item.recruit_type == 1"
                    />
                  </div>
                </template>
              </pager>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
    </div>
  </div>
</template>

<script>
import Pager from "@/components/ArtPager";
import RecruitLine from "./RecruitLine.vue";
import { getAllRecruits } from "@/api/recruit";

export default {
  name: "RecruitSquare",
  components: {
    RecruitLine,
    Pager
  },
  data() {
    return {
      loading: false,
      activeName: "2",
      tabPosition: "top",
      recruits: [],
      row: {},
      type1: "1",
      type0: "0",
      type2: "all",
      total: null
    };
  },
  methods: {
    getList: getAllRecruits,
    async handleGet() {
      this.loading = true;
      try {
        const data = await getAllRecruits();
        const recruit_data = data.data;
        this.row = recruit_data;
        this.recruits = recruit_data;
        this.loading = false;
      } catch (err) {
        this.$message({
          type: "error",
          message: err
        });
      }
    },
    handleClick() {
      if(this.activeName == 0)
      {
        this.$refs.objectRecruit.turn(1)
      } else if (this.activeName == 1) {
        this.$refs.subjectRecruit.turn(1)
      } else {
        this.$refs.refresh.turn(1)
      }
    },
  }
};
</script>

