  <div class="manage-check manage-dev">
    <div class="title-line">
      <p class="title">
        
      </p>
      <p class="org">
        {{ BELONGTO_LIST[user_info.org] }}
      </p>
    </div>
    <!--  -->
    <div v-if="canPanel" class="select-belong">
      <div class="select-display">
        <FitForm ref="form_belong" :mirrors="mirrors_belongs" />
        <FitForm ref="form_type" :mirrors="mirrors_type" />
      </div>
      <div class="select-button">
        <el-button class="belongs-button" type="success" @click="select()">
          
        </el-button>
        <el-button class="belongs-button" type="info" @click="resetAll()">
          
        </el-button>
      </div>
    </div>
    <el-tabs v-model="status" class="table-tab" @tab-click="handleClick">
      <el-tab-pane label="" name="all"></el-tab-pane>
      <el-tab-pane label="" name="0"></el-tab-pane>
      <el-tab-pane label="" name="1"></el-tab-pane>
      <el-tab-pane label="" name="3"></el-tab-pane>
      <el-tab-pane label="" name="2"></el-tab-pane>
    </el-tabs>
    <pager
      ref="refresh"
      :page-size="6"
      :request-func="getList"
      :error-msg="''"
      :extra-data="status"
      :belong-data="belongs"
      :type-data="project_types"
    >
      <el-table ref="filterTable" v-loading="loading" slot-scope="props" border class="table-project" :data="props.list">
        <el-table-column label="" width="160">
          <template slot-scope="scope">
            <div class="line">
              {{ scope.row.team_name }}
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="projecta_name"
          label=""
          width="160"
        >
          <template slot-scope="scope">
            <div class="line">
              {{ scope.row.project_name }}
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="project_type"
          label=""
          width="160"
        >
          <template slot-scope="scope">
            <div class="line">
              {{ PRACTICE_TYPE_LIST[scope.row.project_type] }}
            </div>
          </template>
        </el-table-column>
        <el-table-column
          prop="status"
          label=""
          width="180"
        >
          <template slot-scope="scope">
            <div class="line">
              {{ scope.row.status | statusToReadable }}
            </div>
          </template>
        </el-table-column>
        <el-table-column label="" width="240">
          <template slot-scope="scope">
            <div class="button-lists">
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  v-show="canPass(scope.row)"
                  icon="el-icon-success"
                  circle
                  type="success"
                  class="button-lists-button"
                  size="small"
                  @click="projectPass(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  v-show="canReject(scope.row)"
                  icon="el-icon-error"
                  circle
                  type="danger"
                  class="button-lists-button"
                  size="small"
                  @click="projectReject(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  icon="el-icon-edit"
