                ></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  v-show="canRemake(scope.row)"
                  icon="el-icon-back"
                  circle
                  type="danger"
                  size="small"
                  class="button-lists-button"
                  @click="projectRemake(scope.row)"
                ></el-button>
              </el-tooltip>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </pager>
    <el-dialog v-dialogDrag title="" :visible.sync="dialogVisible" :fullscreen="isPhone">
      <TeamInfoDisplay :team-info="dialogRow" :teammates-info="teammates" />
      <el-button
        round
        type="success"
        :disabled="!canPass(dialogRow)"
        @click="projectPass(dialogRow)"
      >
        
      </el-button>
      <el-button
        round
        type="danger"
        :disabled="!canReject(dialogRow)"
        @click="projectReject(dialogRow)"
      >
        
      </el-button>
      <el-button
        round
        type="danger"
        :disabled="!canRemake(dialogRow)"
        @click="projectRemake(dialogRow)"
      >
        
      </el-button>
      <el-button round type="success" size="small" @click="Suggest(dialogRow)">
        
      </el-button>
    </el-dialog>
    <el-dialog v-dialogDrag title="" :visible.sync="suggestDialogVisible" :fullscreen="isPhone">
      <el-input
        v-model="suggestion"
