        class="suggest-content"
        :rows="4"
        :cols="10"
        placeholder=""
      ></el-input>
      <el-button round type="success" @click="submitSuggestion()">
        
      </el-button>
      <el-button round type="info" @click="suggestDialogVisible = false">
        
      </el-button>
    </el-dialog>
    <!--  -->
  </div>
