<script>
import Pager from '@/components/ArtPager'
import TeamInfoDisplay from '@/components/TeamInfoDisplay'
import statusMixin from '@/mixins/status'
import { mapGetters, mapMutations } from 'vuex'
import {
  getTeammateInfo,
  getAdminTeams,
  auditTeam,
  withDraw,
  suggestion
} from '@/api/team'
import { showAsynConfirm } from '@/mixins/messageBox'
import { setTimeout } from 'timers'
import FitForm from '@/components/FitForm.vue'
import { BELONGTOS, PRACTICE_TYPES } from '@/mixins/consts.js'

const mirrors_belongs = [
  {
    type: 'select',
    label: '',
    prop: 'belongs',
    options: BELONGTOS
  }
]

const mirrors_type = [
  {
    type: 'select',
    label: '',
    prop: 'project_types',
    options: PRACTICE_TYPES
  }
]

export default {
  name: 'ManageCheck',
  components: {
    Pager,
    TeamInfoDisplay,
    FitForm
  },
  mixins: [statusMixin],
  data() {
    return {
      mirrors_belongs,
      mirrors_type,
      loading: false,
      // 
      belongs: 0,
      // 
      project_types: 0,
      // 
      suggestion: '',
      suggestDialogVisible: false,
      timelyRow: '',
      // 
      dialogVisible: false,
      dialogRow: {},
      teammates: [],
      status: 'all', // table
      projects: [],
      PRACTICE_TYPE_LIST: [
        '',
        '70',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ],
      BELONGTO_LIST: [
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        '',
        ''
      ]
    }
  },
  // mounted() {
  //   console.log(this.user_info)
  // },
  computed: {
    ...mapGetters({
      user_info: 'user_info',
      isPhone: 'isPhone',
      currentTeamId: 'currentTeamId',
      canPanel: 'canPanel'
    })
  },
  methods: {
    ...mapMutations({
      saveCurrentTeamId: 'saveCurrentTeamId'
    }),
    // pager
    getList: getAdminTeams,
    async getTeams() {
      this.loading = true
      try {
        const data = await getAdminTeams(this.status)
        this.projects = data.data
        this.loading = false
      } catch (message) {
        this.$message({
          type: 'error',
          message: '  ' + message
        })
      }
    },
    async getTeammateInfo() {
      this.loading = true
      console.log(this.user_info)
      try {
        const data = await getTeammateInfo(this.currentTeamId)
        this.teammates = data.data
        this.loading = false
      } catch (message) {
        this.$message({
          type: 'error',
          message: '  ' + message
        })
      }
    },
    // belongs
    async select() {
      const form_1 = this.$refs['form_belong']
      const form_2 = this.$refs['form_type']
      const data_1 = form_1.getData()
      const data_2 = form_2.getData()
      console.log(data_1, data_2)
      this.belongs = data_1.belongs
      this.project_types = data_2.project_types
      this.loading = true
      setTimeout(() => {
        this.changePages(1)
        this.loading = false
      }, 500)
    },
    // 
    resetAll() {
      const form_1 = this.$refs['form_belong']
      const form_2 = this.$refs['form_type']
      const data_1 = form_1.setData(0)
      const data_2 = form_2.setData(0)
      console.log(data_1, data_2)
      this.belongs = 0
      this.project_types = 0
      this.loading = true
      setTimeout(() => {
        this.changePages(1)
        this.loading = false
      }, 500)
    },
    // 
    handleClick() {
      setTimeout(() => {
        this.changePages(1)
      }, 200)
    },
    // teams
    openDialog(row) {
      this.dialogRow = row
      this.saveCurrentTeamId(row.team_id)
      setTimeout(() => {
        this.getTeammateInfo()
      }, 100)
      setTimeout(() => {
        this.dialogVisible = true
      }, 300)
    },
    _check(row, msg, boolean) {
      showAsynConfirm(msg, '', done => {
        this._auditTeam(row.team_id, boolean)
        done()
        this.changePages(1)
      })
    },
    // 
    projectPass(row) {
      this._check(row, '', 1)
    },
    // 
    projectReject(row) {
      this._check(row, '', 0)
    },
    // 
    projectRemake(row) {
      showAsynConfirm(
        '',
        '',
        done => {
          this._withDraw(row, done)
        }
      )
    },
    // 
    async _auditTeam(team_id, data) {
      try {
        const return_data = await auditTeam(team_id, data)
        this.changePages(1)
        this.$message({
          type: 'success',
          message: ''
        })
      } catch (message) {
        this.$message({
          type: 'error',
          message: '' + message
        })
      }
    },
    async _withDraw(row, done) {
      done()
      try {
        const data = await withDraw(row.team_id)
        this.changePages(1)
        this.$message({
          type: 'success',
          message: ''
        })
      } catch (message) {
        this.$message({
          type: 'error',
          message: '' + message
        })
      }
      this.changePages(1)
    },
    canPass(row) {
      return row.status == 1 || row.status == 2
    },
    // 
    canReject(row) {
      return row.status == 1 || row.status == 3
    },
    // 
    canRemake(row) {
      return row.status == 2
    },
    // dialog
    Suggest(row) {
      this.suggestDialogVisible = true
      this.timelyRow = row
      this.saveCurrentTeamId(row.team_id)
    },
    submitSuggestion() {
      showAsynConfirm('', '', done => {
        this._submitSuggestion(done)
      })
    },
    async _submitSuggestion(done) {
      done()
      try {
        const data = await suggestion(this.currentTeamId, this.suggestion)
        this.$message({
          type: 'success',
          message: ''
        })
        this.suggestDialogVisible = false
        this.suggestion = ''
        this.changePages(1)
      } catch (message) {
        this.$message({
          type: 'error',
          message: '  ' + message
        })
      }
    },
    changePages(i) {
      this.$refs.refresh.turn(i)
    }
  }
}
</script>

