<template>
  <div class="panel-notification panel-dev">
    <div class="board">
      <p class="title">
        
      </p>
      <pager ref="refresh" :page-size="8" :request-func="getList" :error-msg="''">
        <el-table
          v-loading="loading"
          slot-scope="props"
          :data="props.list"
          class="notification-board"
          style="width: 100%"
          border
        >
          <el-table-column label="" width="60" type="expand">
            <template slot-scope="props">
              <LazyDisplayer :info="props.row" />
            </template>
          </el-table-column>
          <el-table-column prop="title" label="">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.type==1" type="primary">
                
              </el-tag>
              <el-tag v-if="scope.row.type == 2" type="success">
                
              </el-tag>
              <el-tag v-if="scope.row.type == 3" type="info">
                
              </el-tag>
              <el-tag v-if="scope.row.type == 4" type="danger">
                
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="title" label=""></el-table-column>
          <el-table-column prop="overtime" label="" width="180"></el-table-column>
          <el-table-column prop="update_time" label="" width="180"></el-table-column>
          <el-table-column label="" width="180">
            <template slot-scope="scope">
              <el-button size="mini" @click="handleEdit(scope.row)">
                
              </el-button>
              <el-button size="mini" type="danger" @click="handleDelete(scope.row)">
                
              </el-button>
            </template>
          </el-table-column>
        </el-table>
      </pager>
    </div>
    <el-dialog
      v-dialogDrag
      title=""
      :visible.sync="edit.visible"
      :fullscreen="isPhone"
      class="notification-edit-dialog"
    >
      <div class="notification-edit-content">
        <el-input v-model="edit.row.title" class="edit-title" placeholder=""></el-input>
        <div class="container">
          <el-input
            id="info-notification-mce"
            v-model="edit.row.content"
            v-loading="edit.loading"
            type="textarea"
            :rows="4"
            :cols="10"
            class="editor-content"
            placeholder=""
          ></el-input>
        </div>
        <div class="block">
          <span class="demonstration">
            
          </span>
          <el-date-picker
            v-model="edit.row.overtime"
            type="datetime"
            placeholder=""
            value-format="yyyy-MM-dd HH:mm:ss"
          ></el-date-picker>
        </div>
        <div class="type-radio">
          <span class="demonstration">
            
          </span>
          <el-radio-group v-model="edit.row.type">
            <el-radio-button label="1">
              
            </el-radio-button>
            <el-radio-button label="2">
              
            </el-radio-button>
            <el-radio-button label="3">
              
            </el-radio-button>
            <el-radio-button label="4">
              
            </el-radio-button>
          </el-radio-group>
        </div>
        <el-button type="primary" @click="submitEdit()">
          
        </el-button>
        <el-button @click="edit.visible = false">
          
        </el-button>
      </div>
    </el-dialog>
    <div class="editor">
      <p class="title">
        
      </p>
      <el-input v-model="currentNotification.title" placeholder=""></el-input>
