<script>
import Pager from '@/components/ArtPager'
import { mapGetters } from 'vuex'
import { showAsynConfirm } from '@/mixins/messageBox'
import {
  getAllNotification,
  addNotification,
  editNotification,
  deleteNotification
} from '@/api/notification'
import LazyDisplayer from '@/components/LazyDisplayer.vue'
import { getNotification } from '@/api/notification'

export default {
  name: 'PanelNotification',
  components: {
    Pager,
    LazyDisplayer
  },
  data() {
    return {
      content: '',
      loading: false,
      notifications: [],
      user_name: '',
      currentNotification: {
        id: 0,
        content: '',
        title: '',
        overtime: null,
        type: null
      },
      edit: {
        row: {},
        visible: false,
        content: '',
        loading: false
      }
    }
  },
  computed: {
    ...mapGetters({
      isPhone: 'isPhone'
    })
  },
  methods: {
    // 
    getList: getAllNotification,
    async _getAllNotification() {
      this.loading = true
      try {
        const data = await getAllNotification()
        this.notifications = data.data
        this.loading = false
      } catch (message) {
        this.$message.warning(message)
      }
    },
    submit() {
      if (
        this.currentNotification.overtime == null ||
        this.currentNotification.type == null ||
        this.currentNotification.content == '' ||
        this.currentNotification.title == ''
      ) {
        this.$message({
          type: 'warning',
          message: ''
        })
      } else {
        showAsynConfirm('', '', done => {
          this._submit(done)
        })
      }
    },
    async _submit(done) {
      try {
        const data = await addNotification(this.currentNotification)
        this.$message({
          type: 'success',
          message: ''
        })
      } catch (message) {
        var error_message = ''
        if (message.indexOf('content') != -1) {
          this.error_message = ''
        } else if (message.indexOf('title') != -1) {
          this.error_message = ''
        } else {
          this.error_message = message
        }
        this.$message({
          type: 'warning',
          message: '' + this.error_message
        })
      }
      this.changePages(1)
      done()
      this.currentNotification.content = ''
      this.currentNotification.title = ''
      this.currentNotification.overtime = ''
      this.currentNotification.type = ''
    },
    parseForm(row) {
      // 
      const keys = [
        'title',
        'content',
        'overtime',
        'time',
        'type',
        'notification_id'
      ]
      const params = {}
      for (const i in keys) {
        params[keys[i]] = row[keys[i]]
      }
      return params
    },
    async getContent(d) {
      try {
        var data = await getNotification(d)
        console.log(data)
        this.edit.content = data.content
      } catch (e) {
        this.$message({
          type: 'error',
          message: '' + e
        })
      }
    },
    handleEdit(data) {
      this.edit.loading = true;
      (this.edit.visible = true), (this.edit.row = this.parseForm(data))
      this.temp = this.parseForm(data)
      this.getContent(this.edit.row.notification_id)
      setTimeout(() => {
        this.edit.row.content = this.edit.content
        this.edit.loading = false
      }, 1000)
    },
    submitEdit() {
      if (
        this.edit.row.title == this.temp.title &&
        this.edit.row.content == this.temp.content &&
        this.edit.row.type == this.temp.type &&
        this.edit.row.overtime == this.temp.overtime
      ) {
        this.$message({
          type: 'warning',
          message: ''
        })
      } else if (
        this.edit.row.title == '' ||
        this.edit.row.content == '' ||
        this.edit.row.type == '' ||
        this.edit.row.overtime == ''
      ) {
        this.$message({
          type: 'warning',
          message: ''
        })
      } else {
        showAsynConfirm('', '', done => {
          this._submitEdit(done)
        })
      }
    },
    async _submitEdit(done) {
      try {
        const data = await editNotification(
          this.edit.row.notification_id,
          this.edit.row
        )
        done()
        this.$message({
          type: 'success',
          message: ''
        })
      } catch (message) {
        done()
        this.$message({
          type: 'warning',
          message: message
        })
      }
      this.changePages(1)
      this.edit.visible = false
    },
    // 
    handleDelete(row) {
      showAsynConfirm(
        '',
        '',
        done => {
          this._handleDelete(row.notification_id, done)
        }
      )
    },
    // async
    async _handleDelete(id, done) {
      const delete_id = id.toString()
      try {
        const data = await deleteNotification(id)
        done()
        this.$message({
          type: 'success',
          message: ''
        })
      } catch (message) {
        done()
        this.$message({
          type: 'warning',
          message: message
        })
      }
      this.changePages(1)
    },
    onEditorUploadComplete(res) {
      if (res.code == 0) {
        this.$message({
          type: 'success',
          message: ''
        })
      } else {
        this.$message({
          type: 'error',
          message: res.msg
        })
      }
    },
    set() {
      this.$refs.richText.setContent('')
    },
    get() {
      console.log(this.$refs.richText.getContent())
    },
    changePages(i) {
      this.$refs.refresh.turn(i)
    }
  }
}
</script>

