<template>
  <div class="manage-excel manage-dev">
    <div class="title-line">
      <p class="title">Excel</p>
    </div>    
    <div class="button">
      <el-button @click="exportExcel3"></el-button>
    </div>
    <div class="button" v-if="canPanel">
      <el-button @click="exportExcel4"></el-button>
    </div>
    <p>{{ stat6 ? `${remainTime}` : '' }}</p>
    <div class="button" v-if="canPanel">
      <el-button @click="exportExcel6"></el-button>
    </div>
  </div>
</template>

<script>
import { openDownloadDialog } from "@/js/download.js";
import { mapGetters } from 'vuex';

export default {
  name: "ManageExcel",
  data() {
    return {
      stat6: false,
      remainTime: 15
    };
  },
  computed: {
    ...mapGetters({
      canPanel: 'canPanel',
    })
  },
  mounted() {
    document.getElementById("manage-excel").scrollIntoView({block:"end",behavior:"smooth"});
  },
  methods: {
    exportExcel3() {
      openDownloadDialog("/api/outExcelOrgTable/");
      this.$message("");
    },
    exportExcel4() {
      openDownloadDialog("/api/outExcelSchoolTable/");
      this.$message("");
    },
    exportExcel6() {
      if (this.stat6) {
        this.$message("");
      } else {
        this.stat6 = true;
        this.remainTime = 15;
        openDownloadDialog("/api/outExcelSchoolTeammateTable/");
        this.$message("");
        setTimeout(() => {
          this.stat6 = false;
        }, this.remainTime * 1000);
        var down = () => {
          this.remainTime--;
          if (this.remainTime > 0) {
            setTimeout(down, 1000);
          }
        };
        setTimeout(down, 1000);
      }
    }
  }
};
</script>

