<template>
  <div class="panel-material panel-dev">
    <div id="board" class="board">
      <p class="title">
        
      </p>
      <pager ref="refresh" :page-size="8" :request-func="getList" :error-msg="''">
        <el-table
          slot-scope="props"
          border
          :data="props.list"
          class="material-board"
          style="width: 100%"
        >
          <el-table-column
            prop="title"
            sortable
            label=""
          ></el-table-column>
          <el-table-column
            prop="author_id"
            label=""
            sortable
            width="180"
          ></el-table-column>
          <el-table-column prop="create_time" label="" width="180"></el-table-column>
          <el-table-column label="" width="180">
            <template slot-scope="scope">
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  icon="el-icon-download"
                  circle
                  size="mini"
                  class="button-lists-button"
                  @click="handleDownload(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  size="mini"
                  icon="el-icon-delete"
                  circle
                  class="button-lists-button"
                  type="danger"
                  @click="handleDelete(scope.row)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </pager>
    </div>
    <div class="upload">
      <p class="upload-title">
        
      </p>
      <el-input v-model="params.title" class="upload-input" placeholder=""></el-input>
      <el-upload
        ref="uploader"
        class="upload-demo"
        drag
        action="/api/admin/guide"
        :data="uploadData"
        :limit="1"
        :file-list="fileList"
        :auto-upload="false"
        :on-error="onError"
        :on-success="onSuccess"
        multiple
      >
        <i class="el-icon-upload"></i>
        <div class="el-upload__text">
          
          <em></em>
          <br>
        </div>
        <div slot="tip" class="el-upload__tip">
          8MB
        </div>
      </el-upload>
      <el-button class="upload-button" type="primary" @click="commit">
        
      </el-button>
      <el-button class="upload-button" type="primary" @click="checkMine">
        
      </el-button>
      <el-dialog v-dialogDrag title="" :visible.sync="dialogTableVisible" :fullscreen="isPhone">
        <el-table
          v-loading="loading"
          v-dialogDrag
          border
          :data="myMaterials"
          class="material-board"
          style="width: 100%"
        >
          <el-table-column prop="title" label=""></el-table-column>
          <el-table-column prop="create_time" label="" width="180"></el-table-column>
          <el-table-column label="" width="250">
            <template slot-scope="scope">
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  icon="el-icon-download"
                  circle
                  size="mini"
                  class="button-lists-button"
                  @click="handleDownload(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  size="mini"
                  icon="el-icon-delete"
                  circle
                  class="button-lists-button"
                  type="danger"
                  @click="handleDelete(scope.row)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import Pager from '@/components/ArtPager'
import { showAsynConfirm } from '@/mixins/messageBox'
import { getAllGuide, deleteOneGuide } from '@/api/guide'
import { mapGetters } from 'vuex'

export default {
  name: 'PanelMaterial',
  components: {
    Pager
  },
  data() {
    return {
      loading: false,
      dialogTableVisible: false,
      materials: [],
      myMaterials: [],
      params: {
        title: ''
      },
      fileList: []
    }
  },
  methods: {
    handleDownload(row) {
      this.$message({
        type: 'info',
        message: ''
      })
      window.open('/api/guide/' + row.guide_id.toString())
    },
    async _getAllGuides() {
      this.loading = true
      try {
        const data = await getAllGuide()
        this.materials = data.data
        this.loading = false
      } catch (message) {
        this.$message({
          type: 'error',
          message: message
        })
      }
    },
    // 
    getList: getAllGuide,
    // 
    handleDelete(row) {
      showAsynConfirm(
        '',
        '',
        done => {
          this._handleDelete(row.guide_id, done)
        }
      )
    },
    // async
    async _handleDelete(id, done) {
      const delete_id = id.toString()
      try {
        const data = await deleteOneGuide(id)
        this.$message({
          type: 'success',
          message: ''
        })
        done()
        this.changePages(1)
      } catch (message) {
        done()
        this.$message({
          type: 'error',
          message: ''
        })
      }
      this._getAllGuides()
    },
    // 
    commit() {
      if (this.params.title == '') {
        this.$message({
          type: 'warning',
          message: ''
        })
      } else {
        showAsynConfirm('', '', done => {
          this._commit(done)
        })
      }
    },
    _commit(done) {
      // TODO 
      if (this.params.title == '') {
        this.$message({
          type: 'error',
          message: ''
        })
      } else {
        this.$refs.uploader.submit()
      }
      done()
    },
    // 
    onError(err) {
      this.$message({
        type: 'error',
        message: ''
      })
    },
    // 
    onSuccess(response) {
      if (response.success) {
        this.changePages(1)
        this.fileList = []
        this.params.title = null
        this.$message({
          type: 'success',
          message: ''
        })
      } else {
        this.$message({
          type: 'error',
          message: response.err_msg
        })
      }
    },
    async checkMine() {
      this.dialogTableVisible = true
      this.myMaterials = []
      try {
        const data = await getAllGuide()
        for (let i = 0; i < data.total; i++) {
          data.data[i].isCheck = 'false'
          if (data.data[i].author_id == this.user_info.user_id) {
            data.data[i].isCheck = 'true'
            this.myMaterials.push({
              guide_id: data.data[i].guide_id,
              create_time: data.data[i].create_time,
              author_id: data.data[i].author_id,
              title: data.data[i].title,
              update_time: data.data[i].update_time
            })
          }
        }
      } catch (message) {
        this.$message({
          type: 'error',
          message: message
        })
      }
    },
    changePages(i) {
      this.$refs.refresh.turn(i)
    }
  },
  computed: {
    ...mapGetters({
      user_info: 'user_info',
      isPhone: 'isPhone'
    }),
    uploadData() {
      return {
        title: this.params.title
      }
    }
  }
}
</script>

