// requestaxiosaxios
import request from './request'

// 
export function postRecruit(form) {
  // axios
  // Promise
  return request({
    url: '/recruit',
    method: 'post',
    data: form
  })
}

// 
export function getAllRecruits(form) {
  return request({
    url: '/recruit/getAll',
    method: 'get',
    params: form,
  })
}

// 
export function getMyRecruits(form) {
  return request({
    url: '/recruit/getMe',
    method: 'get',
    params: form,
  })
}
