<template>
  <div class="panel-note panel-dev note-all">
    <div class="board">
      <p class="title">
        
      </p>
      <pager ref="refresh" :page-size="24" :request-func="getList" :error-msg="''">
        <el-table
          v-loading="loading"
          slot-scope="props"
          border
          :data="props.list"
          class="note-board"
          style="width: 100%"
        >
          <el-table-column label="" width="60" type="expand">
            <template slot-scope="props">
              <NoteInfo :info="props.row" />
            </template>
          </el-table-column>
          <el-table-column label="" width="100">
            <template slot-scope="scope">
              <el-tag v-if="scope.row.status == 1">
                
              </el-tag>
              <el-tag v-if="scope.row.status == 0" type="warning">
                
              </el-tag>
            </template>
          </el-table-column>
          <el-table-column prop="title" label=""></el-table-column>
          <el-table-column prop="content" label=""></el-table-column>
          <el-table-column label="" width="260">
            <template slot-scope="scope">
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  size="mini"
                  icon="el-icon-edit"
                  circle
                  class="button-lists-button"
                  type="primary"
                  @click="handleEdit(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  v-if="scope.row.status == 0"
                  size="mini"
                  icon="el-icon-message"
                  circle
                  class="button-lists-button"
                  type="success"
                  @click="handleReply(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  v-if="scope.row.status == 1"
                  size="mini"
                  type="info"
                  icon="el-icon-view"
                  circle
                  class="button-lists-button"
                  @click="handleReply(scope.row)"
                ></el-button>
              </el-tooltip>
              <el-tooltip class="item" effect="dark" content="" placement="top">
                <el-button
                  size="mini"
                  type="danger"
                  icon="el-icon-delete"
                  circle
                  class="button-lists-button"
                  @click="handleDelete(scope.row)"
                ></el-button>
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </pager>
    </div>
    <el-dialog v-dialogDrag title="" :visible.sync="dialogReplyVisible" :fullscreen="isPhone">
      <div class="dialog-details">
        <p v-if="reply.row.status == 1" class="warn">
          <b></b>
        </p>
        <p>{{ reply.row.title }}</p>
        <p>{{ reply.row.author_id }}</p>
        <p>{{ reply.row.content }}</p>
        <p>{{ reply.row.create_time }}</p>
        <p v-if="reply.row.status == 1" class="reply">
          {{ reply.row.reply }}
        </p>
        <p v-if="reply.row.status == 1" class="reply">
          {{ reply.row.reply_time }}
        </p>
      </div>
      <el-input
        v-model="reply.content"
        type="textarea"
        class="edit-content"
        :rows="4"
        placeholder=""
      ></el-input>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogReplyVisible = false">
           
        </el-button>
        <el-button type="primary" :loading="reply.loading" @click="submitReply()">
           
        </el-button>
      </span>
    </el-dialog>
    <el-dialog
      v-dialogDrag
      title=""
      :visible.sync="edit.visible"
      :fullscreen="isPhone"
      class="note-edit-dialog"
    >
      <div class="note-edit-content">
        <el-input v-model="edit.row.title" class="edit-title" placeholder=""></el-input>
        <el-input
          v-model="edit.row.content"
          type="textarea"
          class="edit-content"
          :rows="4"
          placeholder=""
        ></el-input>
        <el-button type="primary" @click="submitEdit()">
          
        </el-button>
        <el-button @click="edit.visible = false">
          
        </el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import Pager from '@/components/ArtPager'
import { mapGetters } from 'vuex'
import { showAsynConfirm } from '@/mixins/messageBox'
import { getAllNote, replyNote, putNote, deleteNote } from '@/api/note'
import NoteInfo from '@/components/NoteInfo.vue'

export default {
  name: 'Panelnote',
  components: {
    Pager,
    NoteInfo
  },
  data() {
    return {
      loading: false,
      notes: [],
      edit: {
        row: {},
        visible: false
      },
      reply: {
        row: {},
        note_id: '',
        content: ''
      },
      dialogReplyVisible: false,
      temp: {}
    }
  },
  computed: {
    ...mapGetters({
      isPhone: 'isPhone'
    })
  },
  methods: {
    // pager
    getList: getAllNote,
    // get
    async _getAllNote() {
      this.loading = true
      try {
        const data = await getAllNote()
        this.notes = data.data
        this.loading = false
      } catch (message) {
        this.$message.warning(message)
      }
    },
    handleReply(row) {
      this.dialogReplyVisible = true
      this.reply.note_id = row.note_id
      this.reply.row = row
    },
    submitReply() {
      if (this.reply.content == '') {
        this.$message({
          type: 'warning',
          message: ''
        })
      } else {
        showAsynConfirm('', '', done => {
          this._submitReply(done)
        })
      }
    },
    async _submitReply(done) {
      try {
        console.log(this.reply.note_id, this.reply.content)
        const data = await replyNote(this.reply.note_id, this.reply.content)
        this.dialogReplyVisible = false
        this.$message({
          type: 'success',
          message: ''
        })
        done()
        this.changePages(1)
      } catch (message) {
        done()
        this.$message({
          type: 'warning',
          message: message
        })
      }
    },
    parseForm(row) {
      // 
      const keys = ['title', 'content', 'note_id']
      const params = {}
      for (const i in keys) {
        params[keys[i]] = row[keys[i]]
      }
      return params
    },
    handleEdit(data) {
      (this.edit.visible = true), (this.edit.row = this.parseForm(data))
      this.temp = this.parseForm(data)
    },
    submitEdit() {
      if (
        this.edit.row.title == this.temp.title &&
        this.edit.row.content == this.temp.content
      ) {
        this.$message({
          type: 'warning',
          message: ''
        })
      } else if (this.edit.row.title == '' || this.edit.row.content == '') {
        this.$message({
          type: 'warning',
          message: ''
        })
      } else {
        showAsynConfirm('', '', done => {
          this._submitEdit(done)
        })
      }
    },
    async _submitEdit(done) {
      try {
        const data = await putNote(this.edit.row.note_id, this.edit.row)
        this.$message({
          type: 'success',
          message: ''
        })
      } catch (message) {
        this.$message.warning(message)
      }
      this.changePages(1)
      done()
      this.edit.visible = false
      this.edit.row.title = ''
      this.edit.row.content = ''
    },
    // 
    handleDelete(row) {
      showAsynConfirm('', '', done => {
        this._handleDelete(row.note_id, done)
      })
    },
    // async
    async _handleDelete(id, done) {
      const delete_id = id.toString()
      try {
        const data = await deleteNote(id)
        this.$message({
          type: 'success',
          message: ''
        })
        done()
      } catch (message) {
        done()
        this.$message({
          type: 'error',
          message: ''
        })
      }
      this.changePages(1)
    },
    changePages(i) {
      this.$refs.refresh.turn(i)
    }
  }
}
</script>

