        <el-col :span="12">
          <p><b></b>{{ teamInfo.place }}</p>
        </el-col>
        <el-col>
          <p><b>:</b> {{ teamInfo.plan_begin_time }} -- {{ teamInfo.plan_end_time }}</p>
        </el-col>
      </el-row>
      <el-row class="type-row">
        <el-col :span="24">
          <p> <b></b>{{ PRACTICE_TYPE_LIST[teamInfo.project_type] }}</p>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <div class="container">
            <p><b></b>{{ teamInfo.event_background }}</p>
          </div>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <div class="container">
            <p><b></b>{{ teamInfo.event_aim }}</p>
          </div>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24">
          <div class="container">
            <p><b></b>{{ teamInfo.event_content }}</p>
          </div>
        </el-col>
      </el-row>
      <el-row class="fees">
        <el-col :span="8">
          <p><b></b>{{ teamInfo.fee_transport }}</p>
        </el-col>
        <el-col :span="8">
          <p><b></b>{{ teamInfo.fee_print }}</p>
        </el-col>
        <el-col :span="8">
          <p><b></b>{{ teamInfo.fee_other }}</p>
        </el-col>
      </el-row> 
      <div class="teammate">
        <el-tag class="el-tag" type="info" size="mini"></el-tag>
          <el-row
            id = "teammate-row"
            v-for="(p, i) in teammatesInfo"
            :key="i">
            <el-col :span="8">
              <p><b></b>{{ p.user_name }}</p>
            </el-col>
            <el-col :span="8">
              <p><b></b>{{ p.user_id == teamInfo.leader_id ? "" : (p.user_name == teamInfo.vice_leader_name ? "" : "") }}</p>
            </el-col>
            <el-col :span="8" >
              <p><b></b>{{ p.mobile }}</p>
            </el-col>
          </el-row>
      </div>
      <el-row>
        <el-col :span="12">
          <p><b></b>{{ BELONGTO_LIST[teamInfo.belong] }}</p>
        </el-col>
        <el-col :span="12" v-if="hasDepartemnt">
          <p><b></b>{{ teamInfo.department }}</p>
        </el-col>
      </el-row>
      <el-row class="declare-row">
        <el-col :span="12">
          <p><b></b>{{ teamInfo.teacher_name }}</p>
        </el-col>
        <el-col :span="12">
          <p><b></b>{{ teamInfo.teacher_follow == "1" ? '' : '' }}</p>
        </el-col>
      </el-row>
      <div class="el-row status-row">
        <div class="el-col">
          <p><b></b>{{ teamInfo.status| statusToReadable }}</p>
        </div>
      </div>
      <div class="el-row suggestion-row" v-if="hasSuggestion"> 
        <div class="el-col">
          <p><b></b>{{ teamInfo.suggestion }}</p>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import statusMixin from '@/mixins/status'
import { getByUserId } from "@/api/common";
import { BELONGTO_LIST, PRACTICE_TYPE_LIST, DECLARATION_TYPE_LIST } from '@/mixins/consts'
import { mapGetters, mapMutations } from "vuex";

export default {
  name: 'TeamInfDisplay',
  mixins:[statusMixin],
  props: {
    teamInfo: Object,
    teammatesInfo: Array,
  },
  data() {
    return {
      leader_name: '',
      BELONGTO_LIST, 
      PRACTICE_TYPE_LIST, 
      DECLARATION_TYPE_LIST,
    }
  },
  computed: {
    ...mapGetters({
      showDepartment: "showDepartment"
    }),
    hasSuggestion() {
      if(this.teamInfo.suggestion == null) {
        return false
      } else {
        return true
      }
    },
    hasDepartemnt() {
      if(this.teamInfo.department == null) {
        return false
      } else {
        return true
      }
    }
  },
  mounted() {
    this.getUserName();
    console.log(this.teamInfo)
    setTimeout(() => {
      this.changeShow();
    }, 500);
  },
  methods: {
    ...mapMutations({
      setShowDepartment: "setShowDepartment"
    }),
    async getUserName() {
      try {
        const data = await getByUserId(this.teamInfo.leader_id);
        this.leader_name = data.user_name;
      } catch (e) {
        this.$message({
          type: "error",
          message: "," + e
        });
      }
    },
    changeShow() {
      if(this.teamInfo.belong==1 || this.teamInfo.belong==2) {
        this.setShowDepartment(true)
      } else {
        this.setShowDepartment(false)
      }
      console.log(this.showDepartment)
    } 
  }
}
</script>


