// requestaxiosaxios
import request from './request'

// 
export function postNote(title, content) {
  // axios
  // Promise
  return request({
    url: '/note',
    method: 'post',
    data: {
      title,
      content
    }
  })
}

// 
export function getAllNote(form) {
  return request({
    url: '/note/getAll',
    method: 'get',
    params: form
  })
}

// 
export function getMyNote(form) {
  return request({
    url: '/note/getMe',
    method: 'get',
    params: form
  })
}

// 
export function putNote(id, form) {
  return request({
    url: '/note/' + id,
    method: 'put',
    data: form
  })
}

// 
export function deleteNote(id) {
  return request({
    url: '/note/' + id,
    method: 'delete',
  })
}

//
export function replyNote(id, reply) {
  return request({
    url: '/admin/note/' + id,
    method: 'put',
    data: {
      "reply": reply   
    }
  })
}