// requestaxiosaxios
import request from './request'

// 
export function getCurrentUserInfo() {
  // axios
  // Promise
  return request({
    url: '/cas',
    method: 'get'
  })
}

// 
export function getRedirectUrl() {
  return request({
    url: '/cas',   //
    method: 'post'
  })
}

// guid
export function runCallback(guid) {
  return request({
    url: '/cas/callback',
    method: 'post',
    data: {
      guid: guid
    }
  })
}
