<template>
  <div class="recruit-line">
    <div class="line-head">
      <el-tag :type="type">{{ tag }}</el-tag>
    </div>
    <div class="line-body">
      <div class="container">
        <a class="line-title clickable" @click="showDetails(row)">{{ row.title }}</a>
      </div>
      <div class="blank"></div>
      <div class="container">
        <p class="line-content">{{ row.content }}</p>
      </div>
    </div>
    <div class="line-end">
      <p class="line-author">{{ row.user_name}}</p>
      <p class="line-time">{{ row.update_time }}</p>
    </div>
    <el-dialog
      title=""
      :visible.sync="details.visible"
      v-dialogDrag
      :fullscreen="isPhone"
      class="recruit-details-dialog"
    >
      <div class="recruit-details-content">
        <el-tag v-if="details.row.recruit_type == 1"></el-tag>
        <el-tag type="success" v-else></el-tag>
        <p class="details-title">{{ details.row.title }}</p>
        <p class="details-content">{{ details.row.content }}</p>
        <p class="details-author">{{ details.row.user_name }}</p>
        <p class="details-time">{{ details.row.create_time }}</p>
        <p class="details-time">{{ details.row.update_time }}</p>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters } from "vuex";

export default {
  name: "NoteLine",
  props: {
    row: Object
  },
  data() {
    return {
      details: {
        visible: false,
        row: {}
      },
    };
  },
  methods: {
    showDetails(data) {
      this.details.visible = true;
      this.details.row = data;
      console.log(this.details.row)
      console.log(this.isPhone)
    },
  },
  computed: {
    ...mapGetters({
      isPhone: "isPhone"
    }),
    type() {
      if (this.row.status == 0) {
        return "danger";
      } else {
        if (this.row.recruit_type == 1) {
          return "";
        } else {
          return "success";
        }
      }
    },
    tag() {
      if (this.row.status == 0) {
        return "";
      } else {
        if (this.row.recruit_type == 1) {
          return "";
        } else {
          return "";
        }
      }
    }
  }
};
</script>

